html{
  scrollbar-color: rgba(255, 255, 255, 0.1) rgba(0, 0, 0, 0);
  scrollbar-width: thin;
}
body {
  margin: 0px;
  background: #000000;
  font-family: "Roboto", sans-serif;
}
.navarea{
  grid-area:nav;
}
.content{
  grid-area:content;
}
.footer{
  grid-area:footer;
  background-color: rgb(31, 31, 31);
}
.wrapper{
  display: grid;
  width: 100vw;
  
  max-width: 100%;
  grid-template-areas:
    "nav"
    "content"
    "footer";
  grid-template-rows: 10% auto 15%;
  
}

.nav ul{
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  padding : 0% 2%;
  position: fixed;
  min-width: 100%;
 
}
.nav-left{
  display: flex;
  justify-content: space-between;
  width: 20%;
}
.nav-right{
    display: flex;
    justify-content: space-around;
    width: 15%;
    padding: 0% 3%;
}

a{
  text-decoration: none;
  
  font-family: Roboto,sans-serif;
  font-style: normal;
font-weight: 500;
line-height: 34px;
font-size: 16px;
text-align: center;
letter-spacing: 0.02em;
opacity: 0.8;
color: rgb(255, 255, 255);
}
a:hover{
  opacity: 1;
}
.main-content{
color: white;
padding: 15% 5%;
display: flex;
flex-direction: column;
justify-content: flex-start;
align-items: center;
height: 70vh;
}
li{
  list-style: none;
}
.main-content ul{
  display: flex;
  flex-direction: row;
  width: auto;
  height: auto;
  justify-content: center;
  padding: 0%;
  }
.main-content ul li {
  background: rgba(0, 0, 0, 0) linear-gradient(#711de0, #431185) repeat scroll 0% 0%;
  border-radius: 16px;
  width: 195px;
  height: 160px;
  margin: 0% 1%;
display: flex;
justify-content: center;
align-items: center;

}

.footer{
  display: flex;
  justify-content: flex-start;
  
}
.footer li{
  padding: 2% 2%;
}
button{
  padding: 0% 2%;
width: 94px;
height: 100%;
border-radius: 36px;
border-style: none;
background-color: #711de0;
}
button a{
  font-family: Roboto,sans-serif;
  font-style: normal;
font-weight: bold;
line-height: 34px;
font-size: 16px;
text-align: center;
letter-spacing: 0.02em;
opacity: 1;
color: rgb(255, 255, 255);
}
.main-content li a{
  color:white;
  font-weight: bold;
}
@media (max-width: 1200px) {
.nav ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  position: relative;
}
.nav-left{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.nav-right{
  display: flex;
  flex-direction: column;
  align-items: center;
    width: 100%;
}
.nav li{
  background-color: black;
  border-radius: 20px;
  width: 100%;
  margin: 1% 2%;
}
.nav button{
  padding: 0% 2%;
width: 100%;
height: 100%;
border-radius: 20px;
border-style: none;
background-color: #711de0;
display: flex;
}


  .main-content ul{
    display: flex;
flex-direction: column;
align-items: center;
padding: 1% 2%;
width: 100%;
height:100%

  }
  .main-content ul li{
    background: rgba(0, 0, 0, 0) linear-gradient(#711de0, #431185) repeat scroll 0% 0%;
  border-radius: 16px;
display: flex;
justify-content: center;
align-items: center;
    width: 90%;
height: 20%;
margin: 1% 0%;
  }
}
